.Presentation-body {
  text-align: left;

  height : 50vh;
  background-color: #6f4a37;
  padding : 1vmin;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.text-presentation{
  font-size: 3vmin;
  max-width: 50%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media all  and (orientation: portrait) {
  /* applies to mobiles in portrait mode */



  .Presentation-body {
    height : 50vh;
    background-color: #6f4a37;
    display: block;
  }

  .text-presentation{
    margin-top: 4vh;
    font-size: calc(1vh + 1vw);
    max-width: 90vw;
    margin-left: auto;
    margin-right: auto;
  }

  .pic-presentation {
    height: 20vh;
    pointer-events: none;
    margin-top: 2vh;
    display: block;
    margin-left: auto;
    margin-right: auto
  }
}